<template>
    <div class="BriefWarp">
        <header>
            <router-link to="/storedetails">
                <i class="el-icon-arrow-left"></i>
            </router-link>
            <div><span>商家详情</span></div>
        </header>
        <div class="activities_container">
            <header>活动与属性</header>
            <ul class="actibities_ul">
                <li v-for="a in activities">
                    <span class="sp1" :style="'background-color:#'+a.icon_color">{{a.icon_name}}</span>
                    <span class="sp2">{{a.description}}</span>
                </li>
            </ul>
            <ul class="actibities_ul">
                <li v-for="s in supports">
                    <span class="sp1" :style="'background-color:#'+s.icon_color">{{s.icon_name}}</span>
                    <span class="sp2">{{s.description}}</span>
                </li>
            </ul>
        </div>
        <div class="shop_status_container">
            <router-link to="" class="shop_status_container_a">
                <span class="shop_detail_title">食品监督安全公示</span>
                <div>
                    <span class="identification_detail">企业认证详情</span>
                    <i class="el-icon-arrow-right i1"></i>
                </div>
            </router-link>
            <div class="shop_statu_detail">
                <div class="res-well"></div>
                <div class="check_date">
                    <p>
                        <span class="sp3">监督检查结果：</span>
                        <span class="sp4">差</span>
                    </p>
                    <p>
                        <span class="sp3">监督日期：</span>
                        <span class="sp4"></span>
                    </p>
                </div>
            </div>
        </div>
        <div class="shop_status_info">
            <header class="foot_header">商家信息</header>
            <p>{{storeInfo.name}}</p>
            <p>{{storeInfo.address}} </p>
            <p>营业时间：{{opening_hours}}</p>
            <p>营业执照</p>
            <p>餐饮许可证</p>
        </div>
    </div>
</template>

<script>
    export default {
        name: "ShopBrief",
        data(){
            return{
                p_id:0,
                storeInfo:[],
                supports:[],
                activities:[],
                opening_hours:[]
            }
        },
        created(){
            this.p_id = this.$route.query.p_id;
            console.log(this.p_id);
            this.axios.get("shopping/restaurant/"+this.p_id).then(res=>{
                this.storeInfo = res;
                console.log(this.storeInfo);
                this.supports = this.storeInfo.supports;
                this.activities = this.storeInfo.activities;
                this.opening_hours = this.storeInfo.opening_hours;
            }).catch(err=>{});
        }
    }
</script>

